<template>
  <div :class="$root.themeHomeChange === '1' ? 'supervisionPlan_light' : 'supervisionPlan'">
    <div class="header">
      <span class="left">监督计划</span>
      <div class="right">
        <el-date-picker
          v-model="params.year"
          type="year"
          placeholder="选择年"
          style="margin-right: 15px;"
          value-format="yyyy"
          @change="queryTableList(true)"
        >
        </el-date-picker>
        <el-button
          type="primary"
          icon="el-icon-edit-outline"
          @click="planEntry(true)"
        > 计划录入</el-button>
        <!-- <el-button
          type="warning"
          icon="el-icon-document"
          :plain="$root.themeHomeChange === '1'"
          @click="planEntry(false)"
          :disabled="!(currentRow && currentRow.id && currentRow.status === 0)"
        > 编辑 </el-button>
        <el-button
          type="danger"
          icon="el-icon-delete"
          :plain="$root.themeHomeChange === '1'"
          :disabled="!(currentRow && currentRow.id)"
          @click="deleteTableItem"
        > 删除 </el-button> -->
        <el-button
          type="info"
          icon="el-icon-download"
          @click="exportExcel"
        > 导出</el-button>
      </div>
    </div>
    <el-form
      :inline="true"
      class="selectBox"
      :rules="rules"
      :model="params">
      <!-- <el-button type="primary" style="float: left;">生成报告</el-button> -->
      <span>
        <el-form-item label="计划时间: " prop="planDate">
          <el-input-number
            v-model="params.jhsjBegin"
            style="width: 120px; margin-right: 15px;"
            :min="1"
            :max="12"
            size="mini"
            placeholder="开始时间"
          ></el-input-number>
          <el-input-number
            v-model="params.jhsjEnd"
            style="width: 120px;"
            :min="1"
            :max="12"
            size="mini"
            placeholder="结束时间"
          ></el-input-number>
        </el-form-item>
        <el-form-item label="监督类别: ">
          <el-select
            size="mini"
            v-model="params.type"
            placeholder="请选择"
            style="width: 150px;"
            clearable
          >
            <el-option
              v-for="(item, index) in formOption['supervisionType']"
              :label="item.label"
              :value="item.value"
              :key="index"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="单位名称: ">
          <SelectTree
            style="width: 100%"
            ref="selectTreeRef"
            :props="defaultProps"
            :options="treeData"
            :value="valueId"
            :clearable="true"
            :accordion="true"
            @getValue="getValue($event)"
          />
        </el-form-item>
      </span>
      <span>
        <el-form-item>
          <bda-search-input
            v-model="params.keyword"
            placeholder="请输入关键字"
            @search="queryTableList(true)"
            @clear="queryTableList(true)"
            size="mini"
          >
          </bda-search-input>
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            icon="el-icon-search"
            @click="queryTableList(true)"
          > 查询 </el-button>
        </el-form-item>
      </span>
    </el-form>
    <div class="content">
      <el-table
        :data="tableData"
        style="width: 100%"
        :height="tableHeight"
        highlight-current-row
        @current-change="handleCurrentChange_table"
        v-loading="tableLoading"
        element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
      >
        <el-table-column
          type="index"
          label="序号"
          width="60"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="jdxmmc"
          label="监督项目名称"
          width="100"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="jdnr"
          label="监督内容"
          min-width="180"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="type"
          label="监督类别"
          width="120"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="jdly"
          label="监督领域"
          width="130"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="dwmc"
          label="单位名称"
          width="100"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="phbm"
          label="配合部门"
          width="100"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="nd"
          label="年度"
          width="80"
          align="center">
        </el-table-column>
        <el-table-column
          prop="planTime"
          label="计划开展时间"
          width="100"
          align="center"
          show-overflow-tooltip>
          <template slot-scope="scope">
            {{ scope.row.jhsjBegin + '月' + ' ~ ' + scope.row.jhsjEnd + '月' }}
          </template>
        </el-table-column>
        <el-table-column
          prop="jcdx"
          label="检查对象"
          width="100"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="kzfs"
          label="开展方式"
          width="150"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="wcbz"
          label="完成标志"
          min-width="130"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="remark"
          label="备注"
          min-width="150"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="user"
          label="录入人"
          width="90"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="date"
          label="录入时间"
          width="150"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="params.currentPage"
        :page-sizes="[10, 50, 100, 200]"
        :page-size="params.pageSize"
        background
        layout="prev, pager, next, sizes, jumper, total"
        :total="params.total">
      </el-pagination>
    </div>
    <planEntry v-if="showAdd" @closeDilag="init" :formData_in="currentRow" ></planEntry>
  </div>
</template>

<script>
  import planEntry from "../component/planEntry";
  import Interface from "@/base/mixin/risk-control-center.js";
  import SelectTree from "@/views/components/orgTreeSelect";
  import { API_PATH } from '@/config';
  import axios from "axios";
  export default {
    name: "supervisionPlan",
    mixins: [Interface],
    props: {},
    components: { planEntry, SelectTree },
    data(){
      var check_planDate = (rule, value, callback) => {
        if(!this.params.jhsjBegin && !this.params.jhsjEnd) {
          return callback();
        }
        if (this.params.jhsjBegin && this.params.jhsjEnd) {
          if(this.params.jhsjBegin > this.params.jhsjEnd) {
            return callback(new Error('计划开始时间不得大于计划结束时间'));
          } else {
            return callback();
          }
        } else {
          return callback(new Error('请填写完整计划开展时间'));
        }
      };
      return {
        // 单位选择 变量
        defaultProps: {
          children: "list",
          label: "orgName",
          value: "orgId",
        },
        treeData: [],
        valueId: '',
        
        tableLoading: false,
        tableHeight: window.innerHeight - 245,
        departmentList: [],
        params: {
          pageSize: 10,
          currentPage: 1,
          total: 0,
          year: '',
          unit: ''
        },
        rules: {
          planDate: [
            { required: false, validator: check_planDate, trigger: 'blur' }
          ]
        },
        showAdd: false,
        currentRow: {},
        formOption: {
          supervisionType: [
            {
              label: '职能监督',
              value: '职能监督'
            },
            {
              label: '专业监督',
              value: '专业监督'
            },
            {
              label: '专责监督',
              value: '专责监督'
            },
            {
              label: '安全监督',
              value: '安全监督'
            },
            {
              label: '基层单位日常监督',
              value: '基层单位日常监督'
            }
          ],
          unit: [
            {
              label: '全部',
              value: '全部'
            },
            {
              label: '财务部',
              value: '财务部'
            },
            {
              label: '市场营销',
              value: '市场营销'
            }
          ],
          leadDepartment: [
            {
              label: '全部',
              value: '全部'
            },
            {
              label: '市场营销域',
              value: '市场营销域'
            },
            {
              label: '计划生产域',
              value: '计划生产域'
            }
          ]
        },
        tableData: []
      }
    },
    created() {
      this.init();
    },
    // 监听
    watch: {},
    mounted() {},
    methods: {
      // 初始化
      init() {
        this.currentRow = {};
        this.showAdd = false;
        //获取年份
        this.params.year = new Date().getFullYear().toString();
        this.queryTableList(true);
        this.getOrgData();
      },
      // 查询单位名称
      getOrgData() {
        this.getSupervisionUnitTree().then((rs)=>{
          // window.console.log(rs)
          if(Array.isArray(rs) && rs.length !== 0) {
            // window.console.log(rs)
            this.treeData = rs;
          } else {
            this.treeData = [];
          }
        }).finally(()=>{});
      },
      //导出excel
      exportExcel() {
        let path = '/supervision/plan/export'
        let data = {
          begin: Array.isArray(this.params.planDate) && this.params.planDate[0] ? this.params.planDate[0] : '' ,
          end: Array.isArray(this.params.planDate) && this.params.planDate[1] ? this.params.planDate[1] : '',
          current: this.params.currentPage,
          size: this.params.pageSize,
          keyword: this.params.keyword,
          type:  this.params.type,
          unit: this.params.unit,
          mark:'export',
        }
        axios({
          method: "POST",
          url: API_PATH + path,
          data:data,
          headers: {
            'Content-Type': 'application/json;charset=UTF-8'
          },
          responseType: 'blob'
        }).then(res => {
          const content = res.data
          const blob = new Blob([content])
          let d = new Date();
          const fileName = `监督计划-导出信息-${d.toGMTString()}.xlsx`;
          if ('download' in document.createElement('a')) { // 非IE下载
            const elink = document.createElement('a')
            elink.download = fileName
            elink.style.display = 'none'
            elink.href = URL.createObjectURL(blob)
            document.body.appendChild(elink)
            elink.click()
            URL.revokeObjectURL(elink.href) // 释放URL 对象
            document.body.removeChild(elink)
          } else { // IE10+下载
            navigator.msSaveBlob(blob, fileName)
          }
        }).finally(()=>{
        }).catch(()=>{
        })
      },
      // 查询表格内容
      queryTableList(reset) {
        if(reset) {
          this.setPages();
        }
        let params = {
          begin: Array.isArray(this.params.planDate) && this.params.planDate[0] ? this.params.planDate[0] : '' ,
          end: Array.isArray(this.params.planDate) && this.params.planDate[1] ? this.params.planDate[1] : '',
          current: this.params.currentPage,
          size: this.params.pageSize,
          keyword: this.params.keyword,
          type:  this.params.type,
          unit: this.params.unit,
          year: this.params.year,
          status: 1,
          usepage: true
        };
        this.tableData = [];
        this.tableLoading = true;
        this.getSupervisionList(params).then((rs)=>{
          window.console.log(rs)
          if(rs) {
            this.tableData = rs.rows;
            this.setPages(rs.current, rs.size, rs.records);
          } else {
            this.setPages();
          }
        }).finally(()=>{
          this.tableLoading = false;
        })
      },
      // 设置页码
      setPages(currentPage = 1, pageSize = 10, total = 0) {
        this.params['pageSize'] = pageSize;
        this.params['currentPage'] = currentPage;
        this.params['total'] = total;
      },
      // 表格操作按钮点击
      handleClick(row, type) {
        window.console.log(`类型：${type} , 行内容:`, row);
        this.$router.push({
          path: '/details',
          query: {
            index: '4-2',
            params: window.btoa(window.encodeURIComponent(JSON.stringify({
              name: row.name,
              orderId: row.orderId || this.$route.query.orderId,
            }))),
            actNav: this.$route.query.actNav
          }
        });
      },
      handleSizeChange(val) {
        // window.console.log(`每页 ${val} 条`);
        this.params.pageSize = val;
        this.queryTableList();
      },
      handleCurrentChange(val) {
        // window.console.log(`当前页: ${val}`);
        this.params.currentPage = val;
        this.queryTableList();
      },
      // 表格单选
      handleCurrentChange_table(val) {
        this.getSupervisionDetails({ id: val.id }).then((rs)=>{
          window.console.log(rs)
          if(rs && rs.id) {
            this.currentRow = rs;
          } else {
            this.currentRow = val;
          }
        }).finally(()=>{})
      },
      // 取值
      getValue(item){
        // window.console.log(item)
        this.params.unit = item[1];
        this.queryTableList(true)
      },
      // 计划录入
      planEntry(isEmpty = false) {
        // this.$message.success('计划录入');
        if(isEmpty) {
          this.currentRow = {};
        }
        this.showAdd = true;
      },
      // 删除列表选项
      deleteTableItem() {
        this.$confirm(`此操作将删除选中的监督计划, 是否继续?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          // window.console.log('监督计划录入表单', this.form);
          this.supervisionDelete({ id: this.currentRow.id }).then(()=>{
            this.$message.success('删除成功');
          }).finally(()=>{
            this.init();
          });
        }).catch(() => {});
      },
    }
  }
</script>

