      // 修改表格操作按钮样式
      .el-button--text {
        font-family: MicrosoftYaHei;
        font-size: 10px;
        color: #66B9FF;
        font-weight: 400;
      }
      .el-button {
        border-radius: 5px!important;
      }
      .el-form {
        .el-form-item {
          // margin-bottom: 0px;
        }
        input::-webkit-input-placeholder {
          color: #bbb;
        }
         
        input::-moz-placeholder {
          /* Mozilla Firefox 19+ */
          color: #bbb;
        }
               
        input:-moz-placeholder {
          /* Mozilla Firefox 4 to 18 */
          color: #bbb;
        }
               
        input:-ms-input-placeholder {
          /* Internet Explorer 10-11 */
          color: #ccc;
        }
      }
      // 单选高亮
      .el-table__body tr.current-row>td {
        background-color: rgba(235, 252, 255, 1);
      }
      // 修改按钮样式
      .el-button--primary {
        color: #FFF;
        background: rgba($color: #409EFF, $alpha: .8);
        border: 1px solid rgba($color: #409EFF, $alpha: 1);
        border-radius: 5px;
      }
      .el-button--info {
        color: #666;
        background: rgba($color: #ddd, $alpha: 1);
        border: 1px solid rgba($color: #ddd, $alpha: 1);
        border-radius: 5px;
      }
      
      .el-table__cell {
        border-left:  1px solid rgba(204, 204, 204, 1.0);
        border-right:  -1px solid rgba(204, 204, 204, 1.0);
